@import "../styles/constants";

.SearchHints {
  position: absolute;
  background-color: $bg-accent;
  width: 280px;
  padding: 1rem;
  box-sizing: border-box;
  top: 30px;
}
.SearchHint {
  text-decoration: none;
  cursor: pointer;
}
.SearchHint:hover {
  text-decoration: underline;
  font-weight: bold;
}